  gap: 25px;
  font-family: JetBrains Mono, monospace;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  font-size: 60px;
  font-weight: 500;
}

.label {
  font-weight: 200;
}

